<template>
  <div ref="expandedItem" class="root">
    <v-card :ripple="false" @click="openDescription(description, url)" class="custom-card" :style="getStyle()"
      :width="getMaxWidth()" dark>
      <div class="d-flex">
        <v-avatar :size="getAvatarSize()" class="ma-3" tile>
          <v-img :src="imageUrl" class="avatar-image"></v-img>
        </v-avatar>

        <v-col class="text-column">
          <v-card-title :class="getTitleClass()" v-text="title"></v-card-title>
          <v-card-subtitle :class="getSubtitleClass()" v-text="subtitle"></v-card-subtitle>
        </v-col>
      </div>

      <v-expand-transition>
        <div v-show="show">
          <div style="background-color: rgba(0, 0, 0, 0.4); backdrop-filter: blur(4px);">
            <v-card-text v-text="description"></v-card-text>
          </div>
          <v-card-actions>
            <v-chip @click.stop="openUri(url)" class="ma-2" color="white" text-color="black">
              <v-icon left>mdi-link</v-icon>
              Project Page
            </v-chip>
          </v-card-actions>
        </div>
      </v-expand-transition>
    </v-card>
  </div>
</template>

<script>
/* import ProjectDetail from './ProjectDetail.vue'; */

export default {
  components: {},
  name: 'ProjectCard',
  props: {
    title: String,
    subtitle: String,
    description: String,
    url: String,
    imageUrl: String,
    backgroundImage: String,
    gradientStartColor: {
      type: String,
      default: '#ffffff',
    },
    gradientEndColor: {
      type: String,
      default: '#000000',
    },
  },
  data() {
    return {
      show: false,
    };
  },
  methods: {
    openDescription(description, url) {
      if (description === undefined) {
        this.openUri(url);
        return;
      }
      this.show = !this.show;

      if (this.show) {
        setTimeout(() => {
          this.$refs.expandedItem.scrollIntoView({
            block: 'center',
            behavior: 'smooth'
          });
        }, 125);
      }

      setTimeout(() => {
        document.activeElement.blur();
      }, 125);
    },
    openUri(uri) {
      window.open(uri, uri);
    },
    getGradientBackground() {
      return this.backgroundImage ? null : `linear-gradient(to right, ${this.gradientStartColor}, ${this.gradientEndColor})`;
    },
    getStyle() {
      if (!this.backgroundImage) {
        return {
          background: this.getGradientBackground()
        };
      }

      return {
        backgroundImage: `url(${this.backgroundImage})`,
      };
    },
    getAvatarSize() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 96;
        case "sm":
        case "md":
        case "lg":
        case "xl":
        default:
          return 125;
      }
    },
    getMaxWidth() {
      var screenWidth = window.innerWidth;

      if (screenWidth < 350) {
        return 260;
      }

      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 300;
        case "sm":
          return 320;
        case "md":
          return 360;
        case "lg":
        case "xl":
        default:
          return 460;
      }
    },
    getTitleClass() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "text-h6 text-with-shadow";
        case "sm":
          return "text-h5 text-with-shadow";
        case "md":
          return "text-h4 text-with-shadow";
        case "lg":
        case "xl":
        default:
          return "text-h4 text-with-shadow";
      }
    },
    getSubtitleClass() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "text-with-shadow";
        case "sm":
        case "md":
        case "lg":
        case "xl":
        default:
          return "text-with-shadow";
      }
    },
  },
};
</script>

<style scoped>
.text-with-shadow {
  text-shadow: 0px 0px 8px rgba(0, 0, 0, 0.8);
}

.custom-card {
  border-radius: 18px;
  background-size: cover;
  background-position: center;
}

.avatar-image {
  border-radius: 18px;
}

.text-column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  z-index: 1;
}
</style>